import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import {
  Elements,
  CardElement,
  useStripe,
  useElements,
} from "@stripe/react-stripe-js";
import { getProductoLocalStora } from '../service/tienda'
import { RealizarPago } from '../service/pago'
const stripePromise = loadStripe("pk_test_51HzCfKIUXK91B6BdDh57DKt8DWsbOMzW0cQ4OxG6ERbXMfG12oNzpoej4UbA0xdYgSlFkitGrsMyMWjxnR9EhxyO00NnwYBTLy");

const CheckoutForm = () => {
  const [Total, setTotal] = useState(null);
  const stripe = useStripe();
  const elements = useElements();

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    (async()=>{
        const rest = await getProductoLocalStora();
        let n1 = 0;
        let c1 = 0;
        rest.map(iten => (n1 += iten.precio));
        rest.map(iten => (c1 += iten.cantidad));
        setTotal((n1 * c1).toFixed(2));
    })()
  }, [])
  const handleSubmit = async (e) => {
    e.preventDefault();

    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
    });
    setLoading(true);

    if (!error) {
      // console.log(paymentMethod)
      const { id } = paymentMethod;
      try {
        const data = await RealizarPago(id, Total);
        console.log(data);
        elements.getElement(CardElement).clear();
      } catch (error) {
        console.log(error);
      }
      setLoading(false);
    }
  };

  console.log(!stripe || loading);

  return (
    <form className="card card-body" onSubmit={handleSubmit}>

      <img
        src="https://blog.auriboxconsulting.com/wp-content/uploads/2019/02/reactnative.png"
        alt="React Native"
        className="img-fluid"
      />

      <h3 className="text-center my-2">Price: {Total}$</h3>


      <div className="form-group">
        <CardElement />
      </div>

      <button disabled={!stripe} className="btn btn-success">
        {loading ? (
          <div className="spinner-border text-light" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        ) : (
          "Buy"
        )}
      </button>
    </form>
  );
};
function Pago(props) {
  return (
    <div>
      <Elements stripe={stripePromise}>
        <div className="container p-4">
          <div className="row h-100">
            <div className="col-md-4 offset-md-4 h-100">
              <CheckoutForm />
            </div>
          </div>
        </div>
      </Elements>
    </div>
  );
}

export default Pago;
